.imagenes {
  align-content: center;
  border: 5px solid #3c133f;
  -webkit-border-radius: 11px 11px 25px / 11px 11px 36px;
  border-radius: 11px 11px 25px / 11px 11px 36px;
  color: #ffffff;
  -o-text-overflow: clip;
  text-overflow: clip;
  background: #cc294a;
  -webkit-box-shadow: 1px 1px 1px 0 rgba(0,0,0,0.3) ;
  box-shadow: 1px 1px 1px 0 rgba(0,0,0,0.3) ;
  text-shadow: 1px 1px 1px rgba(0,0,0,0.2) ;
  
}

.imagenes p{
  padding: 4px;
  text-align: center;
}

.imagenes img{
  max-height: 200px;
}

.animate__animated.animate__bounceInLeft{

  --animate-duration: 1.5s;
  --animate-delay: 0.9s;

}